import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
import { PostModel } from '../models/post.model';

@Injectable({
  providedIn: 'root'
})
export class PostService {
  public postList: BehaviorSubject<PostModel[]> = new BehaviorSubject(null);
  public url: string;
  constructor(private http: HttpClient) { }

  public createPost(sendDatas: PostModel): Observable<string> {
    return this.http.post<string>('/request/post/create', sendDatas);
  }

  public getPost(userId: any): Observable<any> {
    const params = new HttpParams()
      .set('userId', userId);
    return this.http.get('/request/post/get', {params});
  }
}
