import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription, Observable } from 'rxjs';

import { UserService } from '../../service/user.service';

import { JwtToken } from '../../models/jwt-token.model';
import { User } from '../../models/user.model';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, OnDestroy {
  public jwtToken: JwtToken;
  public subscription: Subscription;
  public currentUser: User;

  constructor(private userService: UserService) { }

  ngOnInit() {
    // tslint:disable-next-line: max-line-length
    // On subscribe au behaviour subject jwtToken cree dans le service UserService de facon à le mettre a jour dans la variable jwtToken du component à chaque modification
    this.subscription = this.userService.jwtToken.subscribe ((jwtToken: JwtToken) => {
      this.jwtToken = jwtToken;
      this.userService.currentUser.subscribe( currentUser => {
        console.log('TCL: HeaderComponent -> ngOnInit -> this.userService.getCurrentUser()', this.userService.currentUser);
        this.currentUser = currentUser;
        console.log('TCL: HeaderComponent -> ngOnInit -> this.currentUser', this.currentUser);
      });

    });
}

  public logout(): void {
    this.userService.logout();
  }

  ngOnDestroy() {
    // on coupe la subscription lors de la destruction du component
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
